// Article

.c-article {
	@include flex-row(wrap, flex-start, space-between);

	&__block {
		flex: 0 0 30%;
		position: relative;
		margin-bottom: 2rem;

		&:nth-of-type(1) {
			flex: 0 0 55%;
			margin-right: 8rem;
		}

		&:nth-of-type(2) {
			margin-top: 8rem;
			margin-left: 18rem;
		}

		&:nth-of-type(3) {
			margin-top: 4rem;
		}

		&:nth-of-type(4) {
			margin-top: 4rem;
			margin-left: 19rem;
		}

		&:nth-of-type(5) {
			margin-top: 2rem;
			left: -4rem;
		}

		&:nth-of-type(6) {
			flex: 0 0 60%;
			margin-top: 2rem;
			margin-left: 25rem;
		}

		&:nth-of-type(7) {
			flex: 0 0 50%;
			margin-top: 3rem;
			margin-left: 18rem;
		}
	}

	h2 {
		text-align: center;
		flex: 0 0 100%;
	}

	img {
		flex: 1;
		max-width: 15rem;
		display: block;
		position: sticky;
		top: 7rem;
		left: 0;
		margin-top: -2rem;
		border-radius: 50%;
	}
}



